import React,{useEffect, useState} from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import Slider from 'react-slick';

import "../../asset/slick/slick.css"; 
import "../../asset/slick/slick-theme.css";

import clickIcon from '../../asset/icon/detail_icon_white.png';
import emptyHeart from '../../asset/icon/empty_heart_icon.png';
import fullHeart from '../../asset/icon/full_heart_icon.png';

export default function imgDetail(props){
    
    let [clickModal, setClickModal] = useState([]);
    let [clickArea, setClickArea] = useState("");
    let [heart,setHeart] = useState(false);
    
    props.getArea(clickArea);
    function modalFunc(a, i){
        setClickModal(a);
        setTimeout(()=>{setClickModal([])},3000);
    }

    let sliderSetting = {
        dots:true,
        infinite:true,
        speed:500,
        slidesToShow:1,
        slidesToScroll:1
    }

    return(
        <div className="img_detail" >
            <div className="img_detail_container">
                {heart === false ?  <img src={emptyHeart} className="img_detail_like" onClick={()=>{setHeart(!heart);alert("해당 게시물 좋아요버튼을 누르셨습니다.");}}/> :  <img src={fullHeart} className="img_detail_like" onClick={()=>{setHeart(!heart)}}/>}
                    <div className="img_position_box">
                        <img src={props.aptInfo.area[props.index].img} className="test_img"/>
                        {props.aptInfo.area[props.index].position.map((a,i)=>{
                            return(
                                <span className="click_point" style={{top:props.aptInfo.area[props.index].position[i][0], left:props.aptInfo.area[props.index].position[i][1]}} 
                                onMouseOver={()=>{modalFunc(a, i)}}
                                onMouseOut={()=>{clearTimeout(modalFunc)}}
                                onClick={()=>{
                                    setClickArea(props.aptInfo.area[props.index].position[i][2]);
                                }}
                                ></span>
                            )
                        })}
                    </div>
                        {
                            JSON.stringify(clickModal) !== JSON.stringify([]) ? 
                            <span className="tooltip" style={{top:clickModal[0], left:clickModal[1], position:"absolute"}}>{clickModal[2]}</span> 
                            : null
                        }
            </div>
        </div>
    )
}