import React,{useState,useEffect} from 'react';
import { useParams, useNavigate, useLinkClickHandler} from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';

import downArrow from '../../asset/icon/down_arrow_icon.png';
import gongLogo from '../../asset/icon/gong_logo.png';

import '../../asset/detail.css';

export default function AptSize(props){
    
    let [index, setIndex] = useState(0);
    let [slideDown, setSlideDown] = useState(false);
    let [size, setSize] = useState(props.aptArea[0].size);
    props.getSize(index);
    props.getSize2(size);
    
    return(
        <div className="apt_name">
        <div onClick={()=>{setSlideDown(!slideDown)}} className="apt_name_slide">
          <span></span>
          <span>{props.aptName}<span className="side_size">{size}</span></span>
          <img src={downArrow} className={slideDown === true?"rotate180deg":"rotate0deg"}/>
          {/* {slideDown === true ? <span>접기</span> : <span>면적선택하기</span>} */}
        </div>
        {slideDown === true? 
        <div className="apt_size_box">
          <div className="apt_size">
            {
              props.aptArea.map((a,i)=>{
                return(
                  <span onClick={()=>{
                    setIndex(index=i);
                    setSize(a.size);
                    setSlideDown(false); //평형 선택시 false로 change
                  }}  
                  className={i === index? "active" :""} >{a.size}</span>
                  )
                })
              }
          </div>
          <div className="apt_size_add_info">
            {
              props.aptArea.map((a,i)=>{
                return(
                  <div>
                    <span className="">{a.size} - </span>
                    <a href={gongLogo} download="공새로 로고" className="download_btn">마감재PDF</a>
                    <a href={gongLogo} download="공새로 로고" className="download_btn">유상옵션PDF</a>
                  </div>
                )
              })
            }
          </div>
        </div>
        :null}
      </div>
    )
}