import React,{useState,useMemo} from 'react';
import { useMediaQuery } from 'react-responsive';
import { useLocation } from 'react-router';

import '../../asset/detail.css';

import ImgDetail from './ImgDetail';
import SimilarBuilding from './SimilarBuilding';
import AptSize from './AptSize';
import UsedMaterials from './UsedMaterial';
import AptInfo from './AptInfo';
import AptCommunity from './AptCommunity';
import AptPopData from './AptPopData';


export default function AptDetail(){


    const location = useLocation();
    let item = location.state.aptInfo;

    const isResponsive = useMediaQuery({
      query:"(max-width:900px)"
    });

    let [selectIndex, setSelectIndex] = useState(0);
    let [selectArea, setSelectArea] = useState();
    let [selectSize, setSelectSize] = useState();
    const getSize = (idx) => { //면적의 index
      setSelectIndex(idx);
    }
    const getSelectArea = (area) => { //클릭된 구역(ex주방,거실)
      setSelectArea(area);
    }
    const getSize2 = (size) => { //면적
      setSelectSize(size);
    }

    return(
        <div className="apt_detail">
            <div className={!isResponsive?"web":null}>
              <div>
                <ImgDetail aptInfo={item} index={selectIndex} getArea={getSelectArea}/>
                <AptSize aptName={item.name} aptArea={item.area} getSize={getSize} getSize2={getSize2}/>
              </div>
            <div className={!isResponsive? "web2" : null}>
                <UsedMaterials aptName={item.name} aptArea={item.area} size={selectSize} area={selectArea} aptPrimary={item.apt}/>
            </div>
            </div>
            {/* <p className="design_title"/> */}
            {/* <p className="design_title"/> */}
            <div className={!isResponsive? "web3" : null}>
              <AptCommunity/>
              <AptPopData/>
            </div>
            <div className={!isResponsive? "web3" : null}>
              <AptInfo aptInfo={item}/>
              <SimilarBuilding/>
            </div>
        </div>
    );
}
