import logo from './logo.svg';
import './App.css';
import AptSearch1 from './page/aptSearch1/AptSearch1';
import AptSearch2 from './page/aptSearch2/AptSearch2';
import AptDetail from './page/aptDetail/AptDetail';
import Header from './component/Header';
import {Switch, Route, Link} from 'react-router-dom';

function App() {
  
  return (
    <div className="App">
      <Header/>
      <Switch>
        <Route exact path="/" component={AptSearch1} />
        <Route exact path="/search/:search" component={AptSearch2} />
        <Route path="/search/:search/detail/:aptInfo" component={AptDetail} />
      </Switch>
    </div>
  );
}

export default App;
